#cart-products {
  min-width: 500px;
}

#cart-products h2 {
  text-align: center;
  margin: 0;
}

#cart-buttons {
  justify-content: center;
  gap: .5rem;
  margin-top: 1rem;
  display: flex;
}

#cart-price-sum {
  justify-content: flex-start;
  margin-top: 1rem;
  font-weight: 700;
  display: flex;
}

.product-row, #cart-heading {
  grid-template-columns: 6fr 2fr 1fr;
  justify-content: start;
  align-items: center;
  gap: .5rem;
  display: grid;
}

#cart-heading {
  font-weight: 700;
}

.product-row {
  margin: .2rem 0;
  font-size: .9em;
}

#cart-heading > div {
  color: #000;
  border-bottom: 1px solid #00f;
  padding: .2rem;
}

.product-row .price:before {
  content: "$";
}

.product-row .qty {
  justify-content: flex-start;
  gap: 0;
  display: flex;
}

.product-row .quantity {
  color: #fff;
  background-color: #000;
  border: 0;
  border-radius: 15px 0 0 15px;
  outline: 0;
  width: 65px;
  padding: .2rem .6rem;
  font-weight: 700;
}

.product-row .qty button {
  cursor: pointer;
  background-color: orange;
  border: 0;
  border-radius: 0 15px 15px 0;
  padding: .2rem .5rem;
}

.product-row .qty button:focus, .product-row .qty button:hover {
  background-color: #ff0;
  outline: 0;
  box-shadow: 0 0 15px #ff0, 0 0 30px orange;
}

#show-cart {
  cursor: pointer;
  background-color: #0000;
  border: 0;
  outline: 0;
  justify-content: center;
  align-items: center;
  display: flex;
}

#cart-icon {
  fill: #fff;
}

#show-cart:focus #cart-icon, #show-cart:hover #cart-icon {
  fill: #ff0;
}

#show-cart-count {
  color: #fff;
  margin-left: .2rem;
}

#show-cart:focus #show-cart-count, #show-cart:hover #show-cart-count {
  color: #ff0;
}

#cart-checkout, #cart-close {
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: 0;
  border-radius: 15px;
  padding: .4rem .8rem;
  font-size: 1em;
}

#cart-checkout:hover, #cart-close:hover {
  color: #000;
  background-color: orange;
}

#cart-checkout:focus, #cart-close:focus {
  outline: 3px solid orange;
}
